import { Visibility, VisibilityOff } from "@mui/icons-material";
import {
  Avatar,
  Button,
  Checkbox,
  FormControl,
  FormControlLabel,
  Grid,
  IconButton,
  InputAdornment,
  InputLabel,
  OutlinedInput,
  Paper,
  TextField,
  Typography,
} from "@mui/material";
import React from "react";
import { useFormik } from "formik";
import * as Yup from "yup";
import { Link } from "react-router-dom";

const SignUp = () => {
  // formik
  const formik = useFormik({
    initialValues: {
      firstname: "",
      email: "",
    },
    // یه تابعه
    onSubmit: (values) => {
      alert(JSON.stringify(values));
    },
    validationSchema: Yup.object({
      firstname: Yup.string()
        .max(10, "must be 10 characters or less")
        .min(5, "must be 5 characters or more")
        .required("*Required"),
      email: Yup.string().email("invalid email").required("*Required"),

      password: Yup.string()
        .max(7, "must be 7 characters or less")
        .min(5, "must be 5 characters or more")
        .required("*Required"),
    }),
  });

  // btn styles
  const btnstyle = {
    margin: "8px 0px",
  };
  // show & hide password
  const [showPassword, setShowPassword] = React.useState(false);
  const handleClickShowPassword = () => setShowPassword((show) => !show);
  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  };
  // paper styles
  const paperStyle = {
    padding: "30px 20px",
    width: 300,
    height: "70vh",
    margin: "60px auto",
  };
  const headerStyle = { margin: "10px" };

  return (
    <div>
      <Grid>
        <form onSubmit={formik.handleSubmit}>
          <Paper elevation={20} style={paperStyle}>
            {/* header of form */}
            <Grid align="center">
              <Avatar src="data:image/png;base64,"></Avatar>
              <h2 style={headerStyle}>Sign UP</h2>
              <Typography variant="caption">
                Please fill this form to create an account!
              </Typography>
            </Grid>
            {/* end of header of form */}

            <TextField
              id="firstname"
              {...formik.getFieldProps("firstname")}
              sx={{ marginTop: "15px" }}
              label="Name"
              placeholder="Enter name"
              fullWidth
            />
            {formik.touched.firstname && formik.errors.firstname ? (
              <div style={{ color: "red" }}>{formik.errors.firstname}</div>
            ) : null}

            <TextField
              id="email"
              {...formik.getFieldProps("email")}
              sx={{ marginTop: "15px" }}
              label="Email"
              placeholder="Enter email"
              fullWidth
            />
            {formik.touched.email && formik.errors.email ? (
              <div style={{ color: "red" }}>{formik.errors.email}</div>
            ) : null}

            <TextField
              sx={{ marginTop: "15px" }}
              label="Number"
              placeholder="Enter number"
              fullWidth
            />

            {/* password field */}
            <FormControl
              sx={{ marginTop: "15px" }}
              fullWidth
              variant="outlined"
            >
              <InputLabel htmlFor="outlined-adornment-password">
                Password
              </InputLabel>
              <OutlinedInput
                {...formik.getFieldProps("password")}
                id="password"
                placeholder="Enter password"
                type={showPassword ? "text" : "password"}
                endAdornment={
                  <InputAdornment position="end">
                    <IconButton
                      aria-label="toggle password visibility"
                      onClick={handleClickShowPassword}
                      onMouseDown={handleMouseDownPassword}
                      edge="end"
                    >
                      {showPassword ? <VisibilityOff /> : <Visibility />}
                    </IconButton>
                  </InputAdornment>
                }
                label="Password"
              />
              {formik.touched.password && formik.errors.password ? (
                <div style={{ color: "red" }}>{formik.errors.password}</div>
              ) : null}
            </FormControl>

            <FormControl
              sx={{ marginTop: "15px" }}
              fullWidth
              variant="outlined"
            >
              <InputLabel htmlFor="outlined-adornment-password">
                Password
              </InputLabel>
              <OutlinedInput
                placeholder="Confirm password"
                type={showPassword ? "text" : "password"}
                endAdornment={
                  <InputAdornment position="end">
                    <IconButton
                      aria-label="toggle password visibility"
                      onClick={handleClickShowPassword}
                      onMouseDown={handleMouseDownPassword}
                      edge="end"
                    >
                      {showPassword ? <VisibilityOff /> : <Visibility />}
                    </IconButton>
                  </InputAdornment>
                }
                label="Password"
              />
            </FormControl>
            {/* end of password field */}

            {/* checkbox & button */}
            <FormControlLabel
              defaultChecked
              label="I accept the rules"
              control={<Checkbox name="checked" color="primary" />}
            />
            <Button
              style={btnstyle}
              variant="contained"
              fullWidth
              type="submit"
              sx={{
                color: "white",
                bgcolor: "#13AE8C",
                "&:hover": {
                  background: "#13AE8C",
                },
              }}
            >
              Sign UP
            </Button>
            <Typography sx={{ marginTop: "10px" }}>
              I alredy have account
              <Link to="/Login" href="#">
                Log in
              </Link>
            </Typography>
            {/* end of checkbox & button */}
            <Grid marginTop="25%" align="center">
              <Typography color="teal" fontSize="small">
                Developed by Minoo Zarpoosh
              </Typography>
            </Grid>
          </Paper>
        </form>
      </Grid>
    </div>
  );
};

export default SignUp;
